$titolo: clamp(2.8rem, 5vw, 4rem);

.contenitoreComputer {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 380px;
  z-index: 99;
  min-width: 1152px !important;
  min-height: 900px !important;
  font-size: 15px;
}

.contenitoreComputerScreen {
  width: 600px;
  height: 600px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  background-image: url(./../../../assets/img/Gioca/pc.png);
  position: relative;
}

input {
