  left: 54px;
  width: 495px;
  height: 295px;
}

.appScreen {
  position: absolute;
  top: 107px;
  left: 92px;
  width: 455px;
  height: 269px;
  display: flex;
  justify-content: center;
  align-items: center;

  .closeApp {
    position: absolute;
