}

.chromeApp {
  background-color: #fff;
  color: #333333;

  .noInternet {
    background-image: url(./../../../assets/img/Gioca/noInternet.jpg);
    height: 350px;
    width: 350px;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }

  .internet {
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .url {
      background-color: #c1c1c1;
    }

    .tabs {
      width: 100%;
      height: 90%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }

    .tab {
      border-radius: 5px;
      padding: 10px;
      background-color: #5a5f72;
      margin-bottom: 10px;
      cursor: pointer;
      color: #fff;
    }
  }
}

