//toggle

:root {
  --bg-color-1: #2e3d48;
  --bg-color-2: #cbf3ff;
  --btn-1: #8fe6ff;
  --btn-shadow-1: #42d5ff;
  --btn-2: #eee;
  --btn-shadow-2: #ccc;
  --circle: #fff;
  --circle-shadow: #aaa;
}

.container {
  align-items: center;
  box-sizing: border-box;
  background: var(--bg-color-1);
  transition: 0.25s ease-in-out;
  position: absolute;
  width: 455px;
  height: 269px;

  .appTitle {
    position: absolute;
    top: 50px;
    left: 35%;
    color: #688ca5;
  }
}
.container.toggle {
  background-color: var(--bg-color-2);
}

.btn {
  display: block;
  width: 80px;
  height: 50px;
  background-color: var(--btn-1);
  border-radius: 5px; /* 70/2 */
  box-shadow: 0 0 20px 0 var(--btn-shadow-1) inset;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.25s ease-in-out;
  cursor: pointer;
}

.small-circle {
  display: block;
  width: 40px;
  height: 40px;
  background-color: var(--circle);
  border-radius: 5px;
  box-shadow: 2px 2px 5px 0 var(--circle-shadow);
  position: relative;
  top: 50%;
  left: 5px; /* (70-60)/2 */
  transform: translateY(-50%);
  transition: 0.25s ease-in-out;
}

.toggle.btn {
  background-color: var(--btn-2);
  box-shadow: 0 0 20px 0 var(--btn-shadow-2) inset;
}

.toggle.small-circle {
  left: 100%;
  transform: translate(calc(-100% - 5px), -50%);
}

